import React, { Component } from 'react';
import GoodImagePlatform from "../../../GoodImagePlatform/v1.0.0/view/index";
import SkuImagePlatform from "../../../SkuImagePlatform/v1.0.0/view/index";

export default class ReleasePlatformGoodsImage extends Component {
  constructor(props){
    super(props);
  }

  render() {

    return (
        <div>
            <GoodImagePlatform
                saleAttributeData={this.props.saleAttributeData}  //
                updateItemTmplAction={this.props.updateItemTmplAction} //
                itemTmplPublishVo={this.props.itemTmplPublishVo} // 
            />
            <SkuImagePlatform
                saleAttributeData={this.props.saleAttributeData}  //
                updateItemTmplAction={this.props.updateItemTmplAction} //
                itemTmplPublishVo={this.props.itemTmplPublishVo} // 
            />
        </div>
    );
  }
}
