import React, { Component } from 'react';
import GoodImageShop from "../../../GoodImageShop/v1.0.0/view/index"
import SkuImageShop from "../../../SkuImageShop/v1.0.0/view/index";



export default class ReleaseShopGoodsImage extends Component {
  constructor(props){
    super(props);
  }
  render() {
    return (
        <div>
            <GoodImageShop saleAttributeData={this.props.saleAttributeData}  //
                           updateItemTmplAction={this.props.updateItemTmplAction} //
                           itemTmplPublishVo={this.props.itemTmplPublishVo} //
             />
            <SkuImageShop saleAttributeData={this.props.saleAttributeData}  //
                          updateItemTmplAction={this.props.updateItemTmplAction} //
                          itemTmplPublishVo={this.props.itemTmplPublishVo} //

            />
        </div>
    );
  }
}
