export default function (state = { loaded:false }, action = {}) {
    switch (action.type) {
        case ITEM_INFO_LOADING:
            return {
                ...state,
                loading: true,
                loaded: false,
            };
        case ITEM_INFO_SUCCESS:
            return {
                ...state,
                loading: false,
                loaded: true,
                data: action.result
            };
        case ITEM_INFO_FAIL:
            return {
                ...state,
                loading: false,
                loaded: true
            }
        default:
            return state;

    }
}

/**
 * type :
 * "0": ( ),
 * "1": (/),
 * "2": (),
 * "3": ()
 */
export function getItemInfoAction(type, params) {
    let url = null;
    switch (type) {
        // case '0': url = '/item/platform/itemTmplPublish/queryItemInfo';
        case '1': url = '/item/platform/itemTmplPublish/queryItemInfo';
        case '2': url = '/item/seller/itemTmplPublish/querySupplySellerItemInfo';
        case '3': url = '/item/shop/itemTmplPublish/queryItemInfo';
    }
    return {
        types: [ITEM_INFO_LOADING, ITEM_INFO_SUCCESS, ITEM_INFO_FAIL],
        promise: (client) => client.get(url, {
            params: params
        })
    }
}
