/**
 * @file --
 */
import React, { Component } from 'react';
import { Table, Checkbox, message,Radio } from 'jdcloudui';
import './style/index.css';

const RadioGroup = Radio.Group;

export default class OtherFreightInfo extends Component {
  constructor(props) {
    super(props);
  }
  componentWillReceiveProps(nextProps){
    console.log(nextProps)
  }

  radioChange=(e)=>{
    console.log(e.target.value)
    let itemTmplPublishVo = JSON.parse(JSON.stringify(this.props.itemTmplPublishVo));
    itemTmplPublishVo.deliveryInfoVo.deliveryType = e.target.value;
    this.props.updateItemTmplAction(itemTmplPublishVo);
  }

  onChange=(e)=>{
    console.log(e.target.checked)
    let itemTmplPublishVo = JSON.parse(JSON.stringify(this.props.itemTmplPublishVo));
    if(!e.target.checked){
      itemTmplPublishVo.deliveryInfoVo.homeDelivery = 0;
    } else {
      itemTmplPublishVo.deliveryInfoVo.homeDelivery = 1;
    }

    this.props.updateItemTmplAction(itemTmplPublishVo);
  }

  render() {
    return (
      <div> 
          <h3 className = 'h3-title'></h3>
          <div>
            <RadioGroup  defaultValue={1} onChange={this.radioChange}>
              <Radio className='otherMt202' value={1}><span className='smallColor'>)</span></Radio><br/>
              <Radio className='otherMt202' value={2}><span className='smallColor'></span></Radio><br/>
            </RadioGroup>
            <br/>
            <Checkbox defaultValue={false} className='otherMt202' onChange={this.onChange}></Checkbox>
          </div>
      </div>
    );
  }
}
