/**
 * @file - 
 */
import React, { Component } from 'react';
import { Button,Layout,message } from 'jdcloudui';
import './style.css'
import { connect } from 'react-redux';
import inject from "../../../../redux/inject";
import querySaveItemRedux , { querySaveItem } from './redux/itemsave_redux';
import querySubmitItemRedux , { querySubmitItem } from './redux/itemsubmit_redux';
const {Footer} = Layout;

@inject({ 
  querySaveItemRedux,
  querySubmitItemRedux
})
@connect(store => ({
  querySaveItemRedux: store.querySaveItemRedux,
  querySubmitItemRedux: store.querySubmitItemRedux
}), { 
  querySaveItem,
  querySubmitItem
})

export default class ReleaseBtns extends Component {
  constructor(props){
    super(props);
    this.state = {
        submitting:false,//
    }
  }

  //
  handleSubmit = ()=>{
    console.log('')
    let itemTmplPublishVo = this.props.itemTmplPublishVo;
    let itemTmplSkuVoList = itemTmplPublishVo.itemTmplSkuVoList;
    let isRule = true;
    if(!itemTmplPublishVo.itemName){
      message.error('')
      isRule = false;
    }
    if(!itemTmplPublishVo.cid){
      message.error('')
      isRule = false;
    }
    if(itemTmplPublishVo.cid == itemTmplPublishVo.secondCid){
      message.error('')
      isRule = false;
    }
    if(!itemTmplPublishVo.itemPicVoList || itemTmplPublishVo.itemPicVoList.length==0 || !itemTmplPublishVo.itemPicVoList[0].url ){
      message.error('')
      isRule = false;
    }
    if(this.props.calcRule){
      if(this.props.calcRule == 3){
        console.log('')
        itemTmplSkuVoList.map((item,index)=>{
          if(!item.length || !item.width || !item.height){
            message.error('');
            isRule = false;
            return;
          }
        })
      } else if(this.props.calcRule == 1){
        console.log('')
        itemTmplSkuVoList.map((item,index)=>{
          if(!item.weight){
            message.error('');
            isRule = false;
            return;
          }
        })
      }
    }

    if(isRule){
      console.log('')
      this.setState({
        submitting:true
      })
      //
      let itemTmplSkuVoList = itemTmplPublishVo.itemTmplSkuVoList;
      itemTmplSkuVoList.map((item,index)=>{
        let extendSkuFields = [];
        for(let label in item){
          if(label.indexOf('extendSkuFields_')!=-1){
            extendSkuFields.push(item[label]);
            delete item[label];
          }
        }
        item.extendSkuFields = extendSkuFields;
      })
      let itemPicpdfVoList = itemTmplPublishVo.itemPicpdfVoList;
      itemPicpdfVoList.map((item,index)=>{
        delete item.uid;
      })
      console.log('------------')
      console.log(itemTmplPublishVo)

      this.props.querySubmitItem(itemTmplPublishVo,this.props.type,this.props.edit).then((res)=>{
        if(res.code == 0){
          this.setState({
            submitting:false
          })
          console.log('')
        } else {
          if(res.msg){
            message.error(res.msg,2)
          } else {
            message.error('',2)
          }
        }
      })
    }
  }

  //
  handleSave = ()=>{
    console.log('');
    this.setState({
      submitting:true
    })
    let itemTmplPublishVo = this.props.itemTmplPublishVo;
    //
    let itemTmplSkuVoList = itemTmplPublishVo.itemTmplSkuVoList;
    itemTmplSkuVoList.map((item,index)=>{
      let extendSkuFields = [];
      for(let label in item){
        if(label.indexOf('extendSkuFields_')!=-1){
          extendSkuFields.push(item[label]);
          delete item[label];
        }
      }
      item.extendSkuFields = extendSkuFields;
    })
    let itemPicpdfVoList = itemTmplPublishVo.itemPicpdfVoList;
    itemPicpdfVoList.map((item,index)=>{
      delete item.uid;
    })
    console.log('------------')
    console.log(itemTmplPublishVo)

    this.props.querySaveItem(itemTmplPublishVo,this.props.type,this.props.edit).then((res)=>{
      if(res.code == 0){
        this.setState({
          submitting:false
        })
        console.log('')
      } else {
        if(res.msg){
          message.error(res.msg,2)
        } else {
          message.error('',2)
        }
      }
    });
  }

  //
  handleCancel = ()=>{
    console.log('')
  }
  
  render() {
    return (
      <div className='footer'>
        <div className="footer-btns">
            <Button type="primary" size='large' onClick={this.handleSubmit} loading={this.state.submit} className="mr10"
                disabled={this.state.submitting}></Button>
            {this.props.ifSave && this.props.type!=2?
            <Button type="primary" size='large' onClick={this.handleSave} loading={this.state.saveInfo} className="mr10"
                disabled={this.state.submitting}></Button>
            :null}
            <Button size='large' onClick={this.handleCancel}
                disabled={this.state.submitting}></Button>
        </div>
      </div>
    );
  }
}
